import { high } from 'nc-lightapp-front';
import CustSupplierWrapper from './base.js'
import { conf as unitConf } from '../../org/BusinessUnitAllTreeRef/index';
import React,{Component} from 'react'

const { Refer } = high;

class CustSuppilerRef extends Component{
	constructor(props){
		super(props);
		this.state = {
			rootNode:{
				refname:this.props.defaultCheck =='pk_custclass'?'refer-000538' : 'refer-000539',///
				refpk:'root'
			},
		}
	}

	onRadioChange(value){
		let root = this.state.rootNode
		root.refname = value=='pk_custclass'?'refer-000538' : 'refer-000539',
		this.setState({
			rootNode:root
		})

	}
	render(){
		return <CustSupplierWrapper {...this.props} rootNode = {this.state.rootNode} onRadioChange={this.onRadioChange.bind(this)}/>
	}
}

export default function (props = {}) {
	var conf = {
		multiLang: {
			domainName: 'uapbd',
			currentLocale: 'zh-CN',
			moduleId: 'refer_uapbd',
		},

		refType: 'gridTree',
		refName: 'refer-000500',/*  */
		refCode: 'uapbd.supplier.InnerCustSupplierForCustGridTreeRef',
		placeholder: 'refer-000500',/*  */
		queryTreeUrl: '/nccloud/uapbd/ref/CustSupplierFlexTreeRef.do',
		queryGridUrl: '/nccloud/uapbd/ref/CustSupplierFlexGridRef.do',
		columnConfig: [{ name: ['refer-000047', 'refer-000501', 'refer-000502', 'refer-000014', 'refer-000504'], code: ['pk_orgname', 'refcode', 'refname', 'mnecode', 'taxpayerid'] }],/*  ,,,,*/
		isMultiSelectedEnabled: false,
		treeConfig: { name: ['refer-000002', 'refer-000003'], code: ['refcode', 'refname'] },/*  ,*/
		isTreelazyLoad: true,
		isShowUnit: false,
		unitProps: unitConf,
		isShowUsual: true,
		isHasDisabledData: false,
		defaultCheck:'pk_custclass'
	};

	return <CustSuppilerRef {...conf} {...props} />
}
