import { high } from 'nc-lightapp-front';
import {conf as unitProps} from "../../org/BusinessUnitTreeRef/index";
const { Refer } = high;

//

export default function (props = {}) {
    let title = 'refer-000393';/*  */
    if(props.title == 'refer-000393'/*  */
        || props.title == 'refer-000394'/*  */
        || props.title == 'refer-000395'){/*  */
            title = props.title;
    }else{
        props.title = title;
    }
	var conf = {
        refName: title,
        placeholder: title,
		refCode: 'uapbd.refer.pub.CustBankAccGridRef',
		queryGridUrl: '/nccloud/uapbd/ref/CustBankAccGridRef.do',
        isMultiSelectedEnabled: false, 
        columnConfig:[
{
    name: [ 'refer-000012', 'refer-000013','refer-000381','refer-000015','refer-000384' ],/*  ,,,,*/
    code: [ "accnum", "accname", "bankdoc_name", "banktype_name", "currtype_name"  ]
}],
				multiLang: {
			domainName: 'uapbd',
			currentLocale: 'zh-CN',
			moduleId: 'refer_uapbd',
		},

		refType: 'grid',
        isShowUnit:false,
        unitProps: unitProps, 
    };
    return <Refer {...conf} {...props} />
}
